import algoliasearch from 'algoliasearch/lite';
import { InstantSearch, connectSearchBox, Hits, Snippet, Index, Configure, connectStateResults, connectPoweredBy } from 'react-instantsearch-dom';
import PropTypes from 'prop-types';
import SearchIcon from '@mui/icons-material/Search';
import ClearIcon from '@mui/icons-material/Clear';
import IconButton from '@mui/material/IconButton';
import './SearchModal.css';
import flagkor from '../media/flag_KOR.jpg';
import { Scrollbars } from 'react-custom-scrollbars';
import Divider from '@mui/material/Divider';
import AlgoliaLogo from '../media/search-by-algolia.svg'



export default function SearchModal(e) {

    const indexNameProduct = "dev_REIT";
    const indexNameAsset = "dev_REIT_ASSET";
    const indexNameBlog = "dev_REIT_BLOG";

    // const algoliaConfig = {
    //     appId: process.env.REACT_APP_ALGOLIA_APP_ID,
    //     apiKey: process.env.REACT_APP_ALGOLIA_SEARCH_ONLY_API_KEY
    // }

    const algoliaConfig = {
        appId: "0OPR8E10IR",
        apiKey: "a1fbde4e925fa7301481d79a25d22084"
    }

    const searchClient = algoliasearch(
        algoliaConfig.appId,
        algoliaConfig.apiKey
    );

    const searchBox = ({ currentRefinement, isSearchStalled, refine }) => (
        <input
            type="text"
            value={currentRefinement}
            onChange={event => refine(event.currentTarget.value)}
            id='modalSearchInputStyle'
            placeholder='ÌÜµÌï©Í≤ÄÏÉâ üòä'
            autoFocus
        />
    );
    const CustomSearchBox = connectSearchBox(searchBox);

    const PoweredBy = ({ url }) => {
        return (
            <div >
                <a href={url} target="_blank" rel="noopener noreferrer" ><img src={AlgoliaLogo} alt='' /></a>
            </div>
        )
    };
    const CustomPoweredBy = connectPoweredBy(PoweredBy);

    const StateResultsIntegrated = ({ searching, allSearchResults, searchState }) => {
        return (
            <>
                {
                    searchState && searchState.query ? (
                        <Configure hitsPerPage={15} attributesToSnippet={['post_content:70']} snippetEllipsisText=" ¬∑¬∑¬∑" />
                    ) : (
                        <Configure hitsPerPage={3} attributesToSnippet={['post_content:70']} snippetEllipsisText=" ¬∑¬∑¬∑" />
                    )
                }

                <Index indexName={indexNameProduct} >
                    <div className='searchIndexContainer'>
                        <div className='searchResultHeader'>Ìà¨ÏûêÏÉÅÌíà</div>
                        <Hits hitComponent={HitProduct} />
                    </div><br/>
                    <Divider />
                </Index>
                <Index indexName={indexNameAsset} >
                    <div className='searchIndexContainer'>
                        <div className='searchResultHeader' >ÏûêÏÇ∞</div>
                        <Hits hitComponent={HitAsset} />
                    </div><br/>
                    <Divider />
                </Index>
                <Index indexName={indexNameBlog} >
                    <div className='searchIndexContainer'>
                        <div className='searchResultHeader' >Î∏îÎ°úÍ∑∏</div>
                        <Hits hitComponent={HitBlog} />
                    </div><br/>
                    <Divider />
                </Index>
            </>
        );
    };
    const CustomStateResults = connectStateResults(StateResultsIntegrated);




    return (
        <>
            <div id='modalDimmerStyle' onClick={e.onClick}>
                <div id='modalSearchStyle' onClick={(e) => e.stopPropagation()}>

                    <InstantSearch indexName={indexNameProduct} searchClient={searchClient}  >

                        <div id='modalSearchBarStyle'>
                            <div style={{ paddingTop: '10px', paddingRight: '15px' }}>
                                <SearchIcon sx={{ fontSize: 40, color: '#385723' }} />
                            </div>
                            <CustomSearchBox showLoadingIndicator />
                            <IconButton aria-label="delete" onClick={e.onClick}>
                                <ClearIcon sx={{ fontSize: 40 }} />
                            </IconButton>
                        </div>

                        <div id='modalSearchResultsContainer'>
                            <Scrollbars autoHeight={false} >
                                <CustomStateResults />
                            </Scrollbars>
                        </div>
                        <div id='modalFooter' >
                            <CustomPoweredBy />
                        </div>
                    </InstantSearch>
                </div>
            </div>

        </>
    )
}

function HitProduct(props) {

    const product_name_kor = props.hit.product_name_kor;
    const product_name_eng = props.hit.product_name_eng;
    const product_type = props.hit.product_type;
    const product_ticker = props.hit.prodcut_ticker;
    const product_nationality_icon = props.hit.product_nationality_icon;

    return (
        <SearchResultCard
            title={product_name_kor}
            subtitle={product_name_eng}
            snippet={null}
            producttype={product_type}
            ticker={product_ticker}
            flag={product_nationality_icon}
            facade={null}
            thumbnail={null}
        />
    );
}

function HitAsset(props) {

    const asset_name = props.hit.asset_name;
    const asset_address = props.hit.asset_address;
    const product_name_kor = props.hit.product_name_kor;
    const product_ticker = props.hit.product_ticker;
    const asset_facade = props.hit.asset_facade;

    return (
        <SearchResultCard
            title={asset_name}
            subtitle={asset_address}
            snippet={null}
            producttype={product_name_kor}
            ticker={null}
            flag={null}
            facade={asset_facade}
            thumbnail={null}
        />
    );
}

function HitBlog(props) {

    const post_name = props.hit.post_name;
    const post_category = props.hit.post_category;
    const post_author = props.hit.post_author;
    const post_date_created = props.hit.post_date_created;
    const post_info = post_category + " ¬∑ by " + post_author + " ¬∑ " + post_date_created;
    const post_snippet = <Snippet attribute="post_content" hit={props.hit} />
    const post_thumbnail = props.hit.post_thumbnail;

    return (
        <SearchResultCard
            title={post_name}
            subtitle={post_info}
            snippet={post_snippet}
            producttype={null}
            ticker={null}
            flag={null}
            facade={null}
            thumbnail={post_thumbnail}
        />
    );
}


HitProduct.propTypes = {
    hit: PropTypes.object.isRequired,
};
HitAsset.propTypes = {
    hit: PropTypes.object.isRequired,
};
HitBlog.propTypes = {
    hit: PropTypes.object.isRequired,
};



const SearchResultCard = (value) => {
    return (
        <div style={{ padding: '10px 0px 10px 0px'}}>
            <div className='searchResultCardContainer' onClick={() => alert('hihi')}>

                <div className='searchResultCardContainer-left'>
                    <span className='searchResultCardContainer-title'>{value.title}</span>
                    <span className='searchResultCardContainer-subtitle'>{value.subtitle}</span>
                    <span className='searchResultCardContainer-snippet' style={value.snippet != null ? {} : { display: 'none' }}>{value.snippet}</span>
                </div>

                <div className='searchResultCardContainer-right'>
                    <span className='searchResultCardContainer-producttype'>{value.producttype}</span>
                    <span className='searchResultCardContainer-ticker' style={value.ticker != null ? {} : { display: 'none' }}>{value.ticker}</span>


                    <div className='searchResultCardContainer-flag' style={ value.flag != null ? {} : { display: 'none' }} >
                        <img src={flagkor} alt=""></img>
                    </div>
                    <div className='searchResultCardContainer-facade' style={value.facade != null ? {} : { display: 'none' }}>
                        <img src={value.facade} alt=""></img>
                    </div>
                    <div className='searchResultCardContainer-thumbnail' style={value.thumbnail != null ? {} : { display: 'none' }}>
                        <img src={value.thumbnail} alt=""></img>
                    </div>

                </div>
            </div>
        </div>
    )
}




