import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Header from './elements/Header';
import Home from "./Home";
import Domestic from "./Domestic";
import Dabs from "./Dabs";
import Blog from "./Blog";
import Overseas from "./Overseas";
import Product from "./Product";
import Post from "./Post";
import Nomatch from "./Nomatch";
import ScrollToTop from "./elements/ScrollToTop";


export default function App() {
  
  console.log('Ïñ¥ÏÑúÏò§ÏÑ∏Ïöîüòé');
  
  return (
    <Router>     
 
      <Header/>
        <ScrollToTop>
              <Switch>
                    <Route exact path="/" component={Home} />
                    <Route exact path="/domestic" component={Domestic} />
                    <Route path="/domestic/:id" component={Product} />
                    <Route path="/overseas" component={Overseas} />
                    <Route path="/dabs" component={Dabs} />
                    <Route exact path="/blog" component={Blog} />
                    <Route path="/blog/:id" component={Post} />
                    <Route component={Nomatch} />
              </Switch>
        </ScrollToTop> 
    </Router>
  );
}

