import './Header.css';
import React, { useState, useEffect } from 'react';
import logo from '../media/logo.svg';
import { NavLink } from 'react-router-dom';
import { useHistory } from "react-router";
import SearchButton from './SearchButton';
import { useMediaQuery } from 'react-responsive';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import ArticleIcon from '@mui/icons-material/Article';
import CloseIcon from '@mui/icons-material/Close';
import Divider from '@mui/material/Divider';
import Paper from '@mui/material/Paper';
import MenuList from '@mui/material/MenuList';
import MenuItem from '@mui/material/MenuItem';
import ListItemIcon from '@mui/material/ListItemIcon';


export default function Header() {

    const [scrollPosition, setScrollPosition] = useState(0);

    const updateScroll = () => {
        setScrollPosition(window.scrollY || document.documentElement.scrollTop);
    }

    useEffect(() => {
        window.addEventListener('scroll', updateScroll);
    });

    const isWide = useMediaQuery({
        query: '(min-width: 1224px)'
    });

    const isWideSearchButton = useMediaQuery({
        query: '(min-width: 750px)'
    });

    const history = useHistory();

    const menuClickHandler = (route) => {
        history.push(route);
        setIsMobileMenuOpen(!isMobileMenuOpen)
    }

    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

    const mobileMenuHandler = () => {
        setIsMobileMenuOpen(!isMobileMenuOpen)
    }

    const mobileMenu = () => {
        return (
            <div className='nav-menu-mobile' onClick={() => { setIsMobileMenuOpen(!isMobileMenuOpen) }}>
                <Paper sx={{ width: 320, maxWidth: '100%' }}>
                    <div style={{ textAlign: 'right' }}>
                        <IconButton onClick={() => { mobileMenuHandler() }}>
                            <CloseIcon style={{ color: '#385723' }} />
                        </IconButton>
                    </div>
                    <Divider />
                    <MenuList>
                        <MenuItem onClick={() => { menuClickHandler('/domestic') }}>
                            <ListItemIcon>
                                <ArticleIcon style={{ color: '#385723' }} />
                            </ListItemIcon>
                            <span className='nav-menu-item-mobile'>Íµ≠ÎÇ¥Î∂ÄÎèôÏÇ∞</span>
                        </MenuItem>
                        <Divider />
                        <MenuItem onClick={() => { menuClickHandler('/overseas') }}>
                            <ListItemIcon>
                                <ArticleIcon style={{ color: '#385723' }} />
                            </ListItemIcon>
                            <span className='nav-menu-item-mobile'>Ìï¥Ïô∏Î∂ÄÎèôÏÇ∞</span>
                        </MenuItem>
                        <Divider />
                        <MenuItem onClick={() => { menuClickHandler('/dabs') }}>
                            <ListItemIcon>
                                <ArticleIcon style={{ color: '#385723' }} />
                            </ListItemIcon>
                            <span className='nav-menu-item-mobile'>DABS</span>
                        </MenuItem>
                        <Divider />
                        <MenuItem onClick={() => { menuClickHandler('/blog') }}>
                            <ListItemIcon>
                                <ArticleIcon style={{ color: '#385723' }} />
                            </ListItemIcon>
                            <span className='nav-menu-item-mobile'>Î∏îÎ°úÍ∑∏</span>
                        </MenuItem>
                        <Divider />
                    </MenuList>
                </Paper>
            </div>
        )
    }

    return (
        <>
            {isMobileMenuOpen && true ? mobileMenu() : null}

            <div id={scrollPosition < 85 ? "header-background" : "header-background-change"}>
                <div id='header-container-default'>
                    <div id='header-container-logo'>
                        <NavLink exact to="/" >
                            <img src={logo} width='123px' alt='moneymill' />
                        </NavLink>
                    </div>
                    <div id='header-container-nav'>
                        <ul className='nav-menu'></ul>
                        <ul className='nav-menu'></ul>

                        {
                            isWideSearchButton && true ?
                                <div className={scrollPosition < 430 ? "nav-menu-searchbutton-disable" : "nav-menu-searchbutton"}>
                                    <SearchButton mode='wide' />
                                </div>
                                :
                                null
                        }

                        {
                            isWide && true ?
                                <>
                                    <ul className='nav-menu'>
                                        <li>
                                            <NavLink to='/domestic' className='nav-menu-item' activeClassName='active' > Íµ≠ÎÇ¥Î∂ÄÎèôÏÇ∞ </NavLink>
                                        </li>
                                        <li>
                                            <NavLink to='/overseas' className='nav-menu-item' activeClassName='active'> Ìï¥Ïô∏Î∂ÄÎèôÏÇ∞ </NavLink>
                                        </li>
                                        <li>
                                            <NavLink to='/dabs' className='nav-menu-item' activeClassName='active'> DABS </NavLink>
                                        </li>
                                        <li>
                                            <NavLink to='/blog' className='nav-menu-item' activeClassName='active'> Î∏îÎ°úÍ∑∏ </NavLink>
                                        </li>
                                    </ul>
                                </>
                                :
                                <ul className='nav-menu'>
                                    {
                                        isWideSearchButton && true ?
                                            null
                                            :
                                            <span className={scrollPosition < 430 ? "nav-menu-searchbutton-disable" : "nav-menu-searchbutton"}>
                                                <SearchButton mode='mobile' />
                                            </span>
                                    }

                                    <IconButton onClick={() => { mobileMenuHandler() }}>
                                        <MenuIcon style={{ color: '#385723', fontSize: '30px' }} />
                                    </IconButton>

                                </ul>
                        }
                    </div>
                </div>
            </div>
        </>
    )
}

