import { useState } from 'react';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import SearchIcon from '@mui/icons-material/Search';
import SearchModal from './SearchModal';
import IconButton from '@mui/material/IconButton';


export default function SearchButton(props) {

    let _mode = props.mode;

    const [isSearch, setIsSearch] = useState(false);

    const isSearchHanlder = () => {
        setIsSearch(!isSearch);
    }

    if (isSearch === false) {
        document.body.style.overflow = "unset";
    } else if (isSearch === true) {
        document.body.style.overflow = "hidden";
    }

    return (
        <>
            {
                isSearch && (
                    <SearchModal onClick={() => isSearchHanlder()}></SearchModal>
                )
            }
            {
                _mode === 'wide' ?
                    <CustomButton
                        disableRipple
                        fullWidth={true}
                        onClick={() => { isSearchHanlder() }}
                    >
                        <SearchIcon sx={{ fontSize: 30, color: '#385723' }} /> Ï¢ÖÎ™©Î™Ö¬∑ÏΩîÎìú¬∑ÎπåÎî©Î™Ö¬∑ÏßÄÏó≠Î™Ö¬∑Ï£ºÏÜå¬∑Ìè¨Ïä§ÌåÖ ÎÇ¥Ïö©ÏùÑ Í≤ÄÏÉâ Ìï¥ Î≥¥ÏÑ∏Ïöî üòä
                    </CustomButton>
                    :
                    <IconButton onClick={() => { isSearchHanlder() }}>
                        <SearchIcon style={{ color: '#385723', fontSize: '30px' }} />
                    </IconButton>
            }
        </>
    )
}

const CustomButton = styled(Button)({
    justifyContent: "flex-start",
    height: '100%',
    borderRadius: '50px',
    boxShadow: 'none',
    textTransform: 'none',
    fontSize: 14,
    padding: '6px 12px',
    lineHeight: 1.5,
    backgroundColor: 'white',
    color: '#757575',
    border: '2px solid #385723',
    fontFamily: [
        'Pretendard-Medium'
    ].join(','),
    textAlign: 'left',
    '&:hover': {
        backgroundColor: '#F5F5F5',
        boxShadow: 'none',
    },
});
