import "./ProductChip.css";
import { useHistory } from "react-router";
import logo from "../media/devfakeasset/logo_sk.png"
import logo2 from "../media/devfakeasset/logo_igisvalue.png"



export default function ProductChip({ data }) {

    const { product_id, product_logo, product_name, product_desc } = data;
    const history = useHistory();
    const chipClickHandler = (product_id) => {
        history.push("/domestic/" + product_id)
    }


    return (
        <div className='productchip-container' id='productchip-container-margin' onClick={() => chipClickHandler(product_id)}>
            <div className='productchip-container-info'>
                <div className='productchip-container-info-name'>
                    {product_name}
                </div>
                <div className='productchip-container-info-desc'>
                    {product_desc}
                </div>
            </div>
            <div className='productchip-container-logo'>
                <img src={logo2} alt="logo" />
            </div>
        </div>
    )
}
