import ProductChipComponent from "../components/ProductChipComponent"
import "./ProductChipContainer.css"
import { useMediaQuery } from 'react-responsive';


export default function ProductChipContainer() {

    const isThreeColumn = useMediaQuery({
        query: '(min-width: 970px)'
    });

    const isTwoColumn = useMediaQuery({
        query: '(min-width: 967px)'
    });

    const isOneColumn = useMediaQuery({
        query: '(min-width: 652px)'
    });

    return (
   

            <div className='productchip-layout-container' >
                <ProductChipComponent />
                
            </div>


    )
}